"use client";
import Link from "next/link";
import { usePathname } from "next/navigation";
import { Button } from "@/components/ui/button";
import { RefreshCw, Moon, Sun } from "lucide-react";
import { motion } from "framer-motion";
import { useEffect, useState } from "react";
import { useTheme } from "next-themes";

export default function Navbar({ onRefresh }) {
  const pathname = usePathname();
  const [provider, setProvider] = useState(null);
  const [mounted, setMounted] = useState(false);
  const { theme, setTheme } = useTheme();

  /* ---------------------------------------------------
     âœ… Load provider + live sync via storage + custom event
  --------------------------------------------------- */
  useEffect(() => {
    setMounted(true);
    if (typeof window === "undefined") return;

    const loadProvider = () => {
      const stored = localStorage.getItem("provider");
      if (stored) {
        try {
          setProvider(JSON.parse(stored));
        } catch (e) {
          console.error("Failed to parse provider:", e);
        }
      }
    };

    loadProvider();

    // ðŸ” Listen for both storage and custom update events
    const handleStorageChange = (event) => {
      if (event.key === "provider") loadProvider();
    };

    const handleCustomEvent = () => loadProvider();

    window.addEventListener("storage", handleStorageChange);
    window.addEventListener("providerUpdated", handleCustomEvent);

    return () => {
      window.removeEventListener("storage", handleStorageChange);
      window.removeEventListener("providerUpdated", handleCustomEvent);
    };
  }, []);

  if (!mounted) return null; // prevent SSR hydration mismatch

  const links = [
    { href: "/", label: "Home" },
    { href: "/jobs", label: "Jobs" },
    { href: "/provider", label: "Provider" },
    { href: "/admin/approve", label: "Admin" },
  ];

  const isActive = (href) => pathname === href;

  return (
    <nav className="w-full backdrop-blur-sm bg-white/80 dark:bg-zinc-900/80 shadow-sm border-b border-orange-100 dark:border-zinc-800 sticky top-0 z-50 transition-colors duration-300">
      <div className="max-w-6xl mx-auto flex items-center justify-between px-6 py-3">
        {/* ðŸ”¸ Logo */}
        <Link
          href="/"
          className="text-2xl font-bold tracking-tight flex items-baseline select-none"
        >
          <motion.span
            className="text-orange-600"
            initial={{ opacity: 0, y: -6 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.5 }}
          >
            Khedme
          </motion.span>
          <motion.span
            className="text-black dark:text-white ml-1 text-[1.6rem] font-extrabold leading-none"
            initial={{ opacity: 0, y: 6, rotate: -8 }}
            animate={{ opacity: 1, y: 0, rotate: 0 }}
            transition={{
              delay: 0.3,
              duration: 0.6,
              type: "spring",
              stiffness: 120,
            }}
            whileInView={{
              scale: [1, 1.05, 1],
              transition: { repeat: Infinity, duration: 3, ease: "easeInOut" },
            }}
          >
            Ø¨Ø§Ù„
          </motion.span>
        </Link>

        {/* ðŸ”¸ Nav Links */}
        <div className="flex items-center space-x-6">
          {links.map(({ href, label }) => (
            <Link
              key={href}
              href={href}
              className={`relative font-medium transition-all duration-200 ${
                isActive(href)
                  ? "text-orange-600 border-b-2 border-orange-600 pb-1"
                  : "text-gray-700 dark:text-gray-300 hover:text-orange-600"
              }`}
            >
              {label}
              {isActive(href) && (
                <motion.div
                  layoutId="activeNav"
                  className="absolute left-0 bottom-0 w-full h-[2px] bg-orange-600 rounded-full"
                />
              )}
            </Link>
          ))}

          {/* ðŸ”¸ Refresh Button (optional) */}
          {onRefresh && (
            <Button
              onClick={onRefresh}
              className="ml-3 bg-orange-500 hover:bg-orange-600 text-white flex items-center gap-1 shadow-sm hover:shadow-md transition"
            >
              <RefreshCw size={16} />
              Refresh
            </Button>
          )}

          {/* ðŸŒ™ Dark Mode Toggle */}
          <Button
            variant="ghost"
            onClick={() => setTheme(theme === "dark" ? "light" : "dark")}
            className="ml-2 text-gray-700 dark:text-gray-300 hover:text-orange-600"
          >
            {theme === "dark" ? <Sun size={18} /> : <Moon size={18} />}
          </Button>

          {/* ðŸ”¸ Provider Welcome + Availability */}
          {provider && (
            <motion.div
              initial={{ opacity: 0, x: 10 }}
              animate={{ opacity: 1, x: 0 }}
              transition={{ delay: 0.2, duration: 0.4 }}
              className="hidden sm:flex items-center gap-2 ml-4 text-gray-700 dark:text-gray-300 font-medium"
            >
              <span>
                ðŸ‘‹ Welcome,{" "}
                <span className="text-orange-600">{provider.full_name}</span>
              </span>
              <span
                className={`text-xs px-2 py-1 rounded-full font-semibold ${
                  provider.available
                    ? "bg-green-100 text-green-600 border border-green-200"
                    : "bg-gray-100 text-gray-600 border border-gray-200"
                }`}
              >
                {provider.available ? "Available" : "Unavailable"}
              </span>
            </motion.div>
          )}
        </div>
      </div>
    </nav>
  );
}
