"use client";
import { ThemeProvider } from "next-themes";
import Navbar from "./Navbar";
import Footer from "./Footer";
import { useState } from "react";
import { Toaster } from "react-hot-toast";

export default function Layout({ children }) {
  const [refreshKey, setRefreshKey] = useState(0);

  const handleRefresh = () => {
    setRefreshKey((prev) => prev + 1);
  };

  return (
    <ThemeProvider
      attribute="class"        // ðŸ‘ˆ important for Tailwind darkMode: 'class'
      defaultTheme="light"     // initial mode
      enableSystem={true}      // optional: respect user's system theme
    >
      <div className="min-h-screen flex flex-col bg-background dark:bg-background-dark text-text dark:text-text-light transition-colors duration-300">
        <Navbar onRefresh={handleRefresh} />
        <main key={refreshKey} className="flex-1 px-4 sm:px-6 md:px-8 py-6">
          {children}
        </main>
        <Footer />
        <Toaster position="top-center" />
      </div>
    </ThemeProvider>
  );
}
